import React, { useState } from 'react';
import bloomist from '../images/WhatsApp Image 2022-03-07 at 2.51.20 PM.jpeg';
import rivana from '../images/WhatsApp Image 2022-03-07 at 2.49.00 PM.jpeg';
import feelunique from '../images/WhatsApp Image 2022-03-07 at 2.38.30 PM.jpeg';
import jsImg from '../images/logo_javascript_22.png';

function Projects() {
    let [live, setLive] = useState([
        {id: 1, title: "boomist", img: bloomist, link: "https://bloomist.net/"},
        {id: 2, title: "rivana", img: rivana, link: "https://rivana.shop/"},
        {id: 3, title: "feelunique", img: feelunique, link: "https://row.feelunique.com/"},
    ]);

    // let bloomist = require("../images/WhatsApp Image 2022-03-07 at 2.51.20 PM.jpeg");
    return ( 
        <div className="projects">
            <h2 className='heading'>
                <span>my projects</span>
            </h2>
            
            {/* live projects */}
            <div className='live-projects'>
                <h2 className='title'>live projects</h2>
                <div className='container'>
                {
                    live.map( function(i) {
                        return(
                            <div className='project' key={i.id}>
                                <div className='project-container'>
                                    <a href={i.link}>
                                        <div className='img-container'>
                                                <img src={i.img} />
                                        </div>
                                    </a>
                                    <h3 className='title'>{i.title}</h3>
                                </div>
                            </div>
                        )
                    })
                }
                </div>
            </div>
            {/* local projects */}
            <div className='live-projects'>
                <h2 className='title'>local projects</h2>
                <div className='container'>
                    <div className='project'>
                        <div className='project-container'>
                            <a href="https://sbedo.me/local-projects/vanilla/">
                                <div className='img-container'>
                                        <img src={jsImg} />
                                </div>
                            </a>
                            <h3 className='title'>vanilla javaScript</h3>
                        </div>
                    </div>
                </div>
            </div>
        </div>
     );
}

export default Projects;